import { Component, OnInit } from '@angular/core';
import { PartyRelationBean } from './bean/party-relation.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
    selector: 'party-relation',
    templateUrl: 'party-relation.html',
    styleUrls: ['party-relation.scss'],
    providers: [ConfirmationService]
})

/**
 * 机构对应关系
 */
export class PartyRelation implements OnInit {

    precredit: string = '机构岗位人员管理';

    // 表格中显示的信息
    tabMesg: any = tableMessage;
    // 表体内容
    tableData: any;
    partyRelationBean: PartyRelationBean = new PartyRelationBean()


    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    showModel: any;
    //模态框是否显示
    display: boolean = false;

    //传入的值
    inValue: string;

    //修改参数
    updateValue: any;

    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService
    ) {
    }

    ngOnInit() {
        this.partyRelationBean.pageSize = '10';
        this.partyRelationBean.pageNum = '1';
        this.queryClick();
    }

    //查询
    queryClick() {
        this.commonHttpService.queryOrgPostTellerRelation(this.partyRelationBean).subscribe(data => {
            if (data.returnCode) {
                if (data.returnCode.code == 'success') {
                    this.tableData = data.orgPostTellerList;
                    this.total = data.total;
             
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //点击查询
    queryC() {
        this.partyRelationBean.flag = '0'
        this.commonHttpService.queryOrgPostTellerRelation(this.partyRelationBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgPostTellerList;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //重置
    doReset() {
        this.partyRelationBean = new PartyRelationBean()
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.partyRelationBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1 + '';
        this.partyRelationBean.pageNum = currentPage;
        //调用查询的方法
        this.queryClick();
        this.partyRelationBean.pageNum = '1';
    }

    // 增加
    add() {
        this.headerTitle = '人员添加';
        this.display = true;
        this.showModel = 1;
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        this.partyRelationBean.pageNum = '1';
        this.queryClick();
    }

    // 修改
    update(item) {
        this.headerTitle = '岗位角色修改';
        this.display = true;
        this.showModel = 2;
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        this.partyRelationBean = new PartyRelationBean();
        this.queryClick();
    }

    //删除
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    postId: item.postId,
                    partyId: item.partyId,
                    userLoginId: item.userLoginId
                }
                this.commonHttpService.deleteOrgPostTellerRelation(param).subscribe(data => {
                    if (data.returnCode) {
                        if (data.returnCode.code == 'success') {
                            this.queryClick()
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });

                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                        }
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }


    //返回的值
    outValue(param) {
        this.display = param.display;
    }
}
