import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { PartyAllComponent } from './party-all.component';//数据权限管理
//机构岗位管理
import { PartyTogetherComponent } from '../party-all/party-together/party-together.component';
//机构岗位人员管理
import { PartyRelation } from '../party-all/party-relation/party-relation';
//机构岗位角色管理
import { PartyRole } from '../party-all/party-role/party-role';


const routes: Routes = [
    {
        path: '',
        component: PartyAllComponent,
        children: [
            { path: '', redirectTo: 'party-together', pathMatch: 'full' },
            {
                path: 'party-together', component: PartyTogetherComponent,
            },
            {
                path: 'party-relation', component: PartyRelation,
            },
            {
                path: 'party-role', component: PartyRole,
            },
        ]
    },
];

export const PartyAllRouting = RouterModule.forChild(routes);