import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { PartyRelationBean } from '../bean/party-relation.bean';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';
import { UserManageBean } from '../../../user-manage/bean/user.manage.bean';
import { PostsManageBean } from '../../../posts-manage/bean/posts-manage.bean'

@Component({
  selector: 'party-add-relation',
  templateUrl: 'party-add-relation.html',
  providers: [CommonHttpService]
})

export class PartyAddRelation implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验
  //实体

  //提示信息
  msgs: Message[] = [];
  //  add_title: string = '产品线管理新增';

  // 表体内容
  tableData: any;
  userManageBean: UserManageBean = new UserManageBean()
  postsManageBean: PostsManageBean = new PostsManageBean()

  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';
  selectedCities: any //选项
  oList: any;//机构标识查询
  jList: any;//岗位标识查询
  partyRelationBean: PartyRelationBean = new PartyRelationBean();
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.userManageBean.pageSize = '10';
    this.userManageBean.pageNumber = '1';
    // this.queryClick();
    // 机构标识查询
    this.commonHttpService.queryVagueOrgList(this.partyRelationBean).subscribe(data => {
      this.oList = data.orgList
    })
    //岗位标识查询
    this.commonHttpService.queryPostById(this.partyRelationBean).subscribe(data => {
      this.jList = data.postInfo
    })
    //
    this.commonHttpService.queryNotExistTellerRelation(this.userManageBean).subscribe(data => {
      this.tableData = data.userList
    })
  }
  //分页
  paginate(event) {
    ;
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1 + '';
    this.userManageBean.pageNumber = currentPage;
    //调用查询的方法
    // this.queryClick();
    this.userManageBean.pageNumber = '1';
  }
  

  // 保存
  preserveClick() {
    //提交服务
    // ;
    let listCustNo = []
    let arr = []
    this.selectedCities.forEach((ri) => {
      let obj = {};
      obj['tellerId'] = this.tableData[ri].roleId
      listCustNo.push(obj)

    });
    listCustNo.forEach((item) => {
      arr.push(item["tellerId"])
    })
    let param = {
      partyId: this.partyRelationBean.partyId,
      postId: this.partyRelationBean.postId,
      roleList: arr
    }
    this.commonHttpService.createOrgPostTellerRelation(param).subscribe(data => {

      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //取消
  doReset() {
    this.outValue.emit(false);
    this.partyRelationBean = new PartyRelationBean()
  }
}