import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CommonHttpService } from '../../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { PartyTogetherBean } from '../bean/partyTogether.bean'
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
@Component({
  selector: 'app-add-something',
  templateUrl: './add-something.component.html',
  providers: [CommonHttpService, ConfirmationService, Commfunc]
})
export class AddSomethingComponent implements OnInit {
  public precredit: string = '机构岗位人员与角色新增';
  //提示信息
  public msgs: Message[] = [];
  public partyTogetherBean: PartyTogetherBean = new PartyTogetherBean()
  list: any = [];//机构模糊查询的下拉框
  list1: any = [];//机构模糊查询的下拉框
  list2: any = [];//岗位模糊查询的下拉框
  list3: any = [];//岗位模糊查询的下拉框
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc
  ) {

  }

  ngOnInit() {
    //机构标识及名称
    this.partyTogetherBean.pageNum = 1,
      this.partyTogetherBean.pageSize = 10,
      this.query()
    this.queryRole()
  }
  //机构查询
  query() {
    let param ={
      pageSize:1000000
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }
  //岗位查询
  queryRole() {
    //岗位标识及名称
    let param ={
      pageSize:1000000
    }
    this.commonHttpService.queryPostById(param).subscribe(data => {
      if (data.postInfoList) {
        // 
        this.list2 = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
        this.list3 = this.commfunc.listToLabelvalue(data.postInfoList, 'postId', 'postName');
      }
    });
  }
  //保存
  queryClick() {
    //新增服务未定
    let param ={
      partyId:this.partyTogetherBean.orgName,
      postId:this.partyTogetherBean.postId
    }
    this.commonHttpService.createOrgPostRelation(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = []
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //重置
  doReset() {
    this.partyTogetherBean = new PartyTogetherBean()
  }
  //取消
  doCancel() {
    this.outValue.emit(false);
    this.partyTogetherBean = new PartyTogetherBean()
  }


  //onchange事件
  doChose(event) {
    this.partyTogetherBean.orgName = this.returnValue(event.value, 'label', 'value');
  }
  doChose1(event) {
    this.partyTogetherBean.orgId = this.returnValue(event.value, 'value', 'label');
  }
  doChose2(event) {
    this.partyTogetherBean.postId = this.returnValue1(event.value, 'label', 'value');

  }

  doChose3(event) {
    this.partyTogetherBean.postName = this.returnValue1(event.value, 'value', 'label');
  }

  /**
   * 转换值
   * @param param  值
   * @param colum  字段
   */
  returnValue(param, colum, valueColum) {
    let value: string = "";
    this.list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
  returnValue1(param, colum, valueColum) {
    let value: string = "";
    this.list2.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
}
