import { Injectable, NgModule } from '@angular/core';

import { AuthorityConfigService } from './authority.config';
import { API } from 'app/@uisftech/common/constant/config';
import { HttpService } from 'app/@uisftech/common/service/http.service1';

@Injectable()
export class AuthorityService{
    constructor(public httpService: HttpService) {
    }
    querybankOrgTree(params){
        return this.httpService.ajax(API.IP+AuthorityConfigService.querybankOrgTree,params,null);
    }
   
}