import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module'
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';
import { PartyTogetherBean } from '../bean/partyTogether.bean';
// import { RoleManageBean } from '../../role-manage/bean/role-manage-bean'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'app-allot-role',
  templateUrl: './allot-role.component.html'
})
export class AllotRoleComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  partyTogetherBean: PartyTogetherBean = new PartyTogetherBean()
  selectedCities: any = [];
  list: any;//接收参数
  msgs: Message[] = [];
  total: number;//接收总条数
  constructor(
    public commonHttpService: CommonHttpService,
  ) { }

  ngOnInit() {
    this.partyTogetherBean.pageNum = 1,
    this.partyTogetherBean.pageSize = 10
    this.query()
  }
  ngOnChanges() {
    this.fuzhi()

  }
  fuzhi() {
    this.partyTogetherBean = this.inValue;
  }

  //初始查询
  query() {
    let param = {
      pageNum:this.partyTogetherBean.pageNum,
      pageSize: this.partyTogetherBean.pageSize
    }
    this.commonHttpService.queryRoleListInfo(param).subscribe(data => {
      this.list = data.roleListInfo

      this.total = data.total;
    })
  }
  // 保存
  preserveClick() {

    let listCustNo = []
    let arr = []
    this.selectedCities.forEach((ri) => {
      let obj = {};
      obj['roleId'] = this.list[ri].roleId
      listCustNo.push(obj)

    });
    listCustNo.forEach((item) => {
      arr.push(item["roleId"])
    })
    let param = {
      partyId: this.partyTogetherBean.partyId,
      postId: this.partyTogetherBean.postId,
      roleList: arr
    }

    //提交服务
    this.commonHttpService.createOrgPostRoleRelation(param).subscribe(data => {

      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  // 取消
  doReset() {
    this.partyTogetherBean = new PartyTogetherBean()
  }

  // //分页
  paginate(event) {
    //每页显示的条数
    this.partyTogetherBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.partyTogetherBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.partyTogetherBean.pageNum = 1;
  }
}
