import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module'
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';
import { PartyRoleBean } from '../bean/party-role.bean';
import { RoleManageBean } from '../../../role-manage/bean/role-manage-bean'
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'party-add-role',
  templateUrl: 'party-add-role.html',
  providers: [CommonHttpService]
})

export class PartyAddRole implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  userform: FormGroup; //校验
  total: string;//接收后台返回的总条数
  //实体

  //提示信息
  msgs: Message[] = [];
  // public add_title: string = '产品线管理新增';
  list: any;//接收参数
  selectedCities: any = [];  //
  partyRoleBean: PartyRoleBean = new PartyRoleBean();
  roleManageBean: RoleManageBean = new RoleManageBean()
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.roleManageBean.pageNum = 1,
      this.roleManageBean.pageSize = 10,
      this.query()

  }
  //新增初始查询
  query() {
    this.commonHttpService.queryRoleListInfo(this.roleManageBean).subscribe(data => {
      this.list = data.roleListInfo
      this.total = data.total;
    })
  }
  // 保存
  preserveClick() {

    let listCustNo = []
    let arr = []
    this.selectedCities.forEach((ri) => {
      let obj = {};
      obj['roleId'] = this.list[ri].roleId
      listCustNo.push(obj)

    });
    listCustNo.forEach((item) => {
      arr.push(item["roleId"])
    })
    let param = {
      partyId: this.partyRoleBean.partyId,
      postId: this.partyRoleBean.postId,
      roleList: arr
    }

    //提交服务
    this.commonHttpService.createOrgPostRoleRelation(param).subscribe(data => {

      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  // 取消
  doReset() {
    this.partyRoleBean = new PartyRoleBean()
  }

  doChose() {
    this.partyRoleBean.roleId = this.partyRoleBean.roleName
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.roleManageBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.roleManageBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.roleManageBean.pageNum = 1;
  }
}