import { Component, OnInit } from '@angular/core';
import { PartyTogetherBean } from './bean/partyTogether.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';



@Component({
  selector: 'app-party-together',
  templateUrl: './party-together.component.html',
  styleUrls: ['./party-together.component.scss'],
  providers: [ConfirmationService, CommonHttpService, Commfunc]
})
export class PartyTogetherComponent implements OnInit {
  precredit: string = '机构岗位人员与角色管理';

  // 表格中显示的信息
  tabMesg: any = tableMessage;
  // 表体内容
  tableData: any;
  partyTogetherBean: PartyTogetherBean = new PartyTogetherBean()


  //总页数
  total: string;
  //每页多少条
  pageSize: number = 10;

  //提示信息
  msgs: Message[] = [];

  //模态框的标题
  headerTitle: string = "修改";
  showModel: any;
  //模态框是否显示
  display: boolean = false;

  //传入的值
  inValue: string;

  //分配人员
  allotUserValue: any;
  //分配角色
  allotRoleValue: any;
  list: any = [];//机构模糊查询的下拉框
  list1: any = [];//机构模糊查询的下拉框
  list2: any = [];//岗位模糊查询的下拉框
  list3: any = [];//岗位模糊查询的下拉框
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
  }

  ngOnInit() {
    this.partyTogetherBean.pageSize = 10;
    this.partyTogetherBean.pageNum = 1;
    this.queryClick();
    this.query()
    this.queryRole()
  }

  //机构查询
  query() {
    let param = {
      pageSize: 1000000
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }
  //岗位查询
  queryRole() {
    //岗位标识及名称
    let param = {
      pageSize: 1000000
    }
    this.commonHttpService.queryPostById(param).subscribe(data => {
      if (data.postInfoList) {
        // 
        this.list2 = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
        this.list3 = this.commfunc.listToLabelvalue(data.postInfoList, 'postId', 'postName');
      }
    });
  }
  //onchange事件
  doChose(event) {
    this.partyTogetherBean.partyName = this.returnValue(event.value, 'label', 'value');
  }
  doChose1(event) {
    this.partyTogetherBean.partyId = this.returnValue(event.value, 'value', 'label');
  }
  doChose2(event) {
    this.partyTogetherBean.postId = this.returnValue1(event.value, 'label', 'value');

  }

  doChose3(event) {
    this.partyTogetherBean.postName = this.returnValue1(event.value, 'value', 'label');
  }
  /**
  * 转换值
  * @param param  值
  * @param colum  字段
  */
  returnValue(param, colum, valueColum) {
    let value: string = "";
    this.list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
  returnValue1(param, colum, valueColum) {
    let value: string = "";
    this.list2.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
  //查询
  queryClick() {
    this.commonHttpService.queryOrgPostRelation(this.partyTogetherBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.roleList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击查询
  queryC() {
    this.partyTogetherBean.flag = '0'
    this.commonHttpService.queryOrgPostRelation(this.partyTogetherBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.roleList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //重置
  doReset() {
    this.partyTogetherBean = new PartyTogetherBean()
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.partyTogetherBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.partyTogetherBean.pageNum = currentPage;
    //调用查询的方法
    this.queryClick();
    this.partyTogetherBean.pageNum = 1;
  }

  // 增加
  add() {
    this.headerTitle = '人员或角色添加';
    this.display = true;
    this.showModel = '1';
  }

  //添加后返回的值
  addCall(param) {
    this.display = param;
    this.partyTogetherBean.pageNum = 1;
    this.queryClick();
  }

  // 分配角色
  allotRole(item) {
    this.headerTitle = '分配角色';
    this.display = true;
    this.showModel = '2';
    this.allotRoleValue = item;
  }

  //分配角色回调
  allotRoleCall(param) {
    this.display = param;
    this.partyTogetherBean = new PartyTogetherBean();
    // this.queryClick();
  }
  // 分配人员
  allotUser(item) {
    this.headerTitle = '分配人员';
    this.display = true;
    this.showModel = '3';
    this.allotUserValue = item;
  }

  //分配人员回调
  allotUserCall(param) {
    this.display = param;
    this.partyTogetherBean = new PartyTogetherBean();
    // this.queryClick();
  }

  //删除
  delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { postId: item.postId, partyId: item.partyId }
        this.commonHttpService.deleteOrgPostRelation(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            // this.partyTogetherBean.pageNum = 1;
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }


  //返回的值
  outValue(param) {
    this.display = param.display;
  }
  //按钮权限
  permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
	}
}
