import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';
import { PartyRoleBean } from '../bean/party-role.bean';

@Component({
    selector: 'party-update-role',
    templateUrl: 'party-update-role.html'
})

export class PartyUpdateRole implements OnInit {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    public msgs: Message[] = [];
    //实体
    public partyRoleBean: PartyRoleBean = new PartyRoleBean();
    constructor(
        public commonHttpService: CommonHttpService
    ) { }

    ngOnInit() {

    }

    ngOnChanges() {
        this.fuzhi()

    }
    fuzhi() {
        this.partyRoleBean = this.inValue;
    }
    //提交
    preserveClick() {
        this.commonHttpService.moidfyOrgPostRoleRelation(this.partyRoleBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });

    }
    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
        this.partyRoleBean = new PartyRoleBean();
    }

}