import { Component, OnInit } from '@angular/core';
import { PartyRoleBean } from './bean/party-role.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
    selector: 'party-role',
    templateUrl: 'party-role.html',
    styleUrls: ['party-role.scss'],
    providers: [ConfirmationService, Commfunc]
})

/**
 * 机构对应关系
 */
export class PartyRole implements OnInit {
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    precredit: string = '机构岗位角色管理';

    // 表体内容
    tableData: any;
    partyRoleBean: PartyRoleBean = new PartyRoleBean()

    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    showModel: any;
    //模态框是否显示
    display: boolean = false;

    //传入的值
    inValue: string;

    //修改参数
    updateValue: any;
    userform: FormGroup; //校验
    list: any = [];//机构模糊查询的下拉框
    list1: any = [];//机构模糊查询的下拉框
    list2: any = [];//岗位模糊查询的下拉框
    list3: any = [];//岗位模糊查询的下拉框
    list4: any = [];//角色模糊查询的下拉框
    list5: any = [];//角色模糊查询的下拉框
    //角色标识
    roleList: any
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {
      
    }

    ngOnInit() {
        this.commonHttpService.queryVaguePostList(this.partyRoleBean).subscribe(data => {
            this.list = data.postInfo
        })
        this.commonHttpService.queryRoleListInfo(this.partyRoleBean).subscribe(data => {
            this.roleList = data.roleListInfo
        })
        this.partyRoleBean.pageSize = '10';
        this.partyRoleBean.pageNum = '1';
        this.queryClick();
        this.userform = this.fb.group({
            'partyId': new FormControl('', Validators.required),
            // 'postId': new FormControl('', Validators.required),
            // 'roleId': new FormControl('', Validators.required),
        });
        this.query();//查询机构
        this.queryPost();//查询岗位
        this.queryRole();//查询角色
    }

    //机构查询
    query() {
        let param = {
            pageSize: "1000000"
        }
        this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
            if (data.orgList) {
                this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
                this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
            }
        })
    }
    //岗位查询
    queryPost() {
        //岗位标识及名称
        let param = {
            pageSize: "1000000"
        }
        this.commonHttpService.queryVaguePostList(param).subscribe(data => {
            if (data.postList) {
                // 
                this.list2 = this.commfunc.listToLabelvalue(data.postList, 'postName', 'postId');
                this.list3 = this.commfunc.listToLabelvalue(data.postList, 'postId', 'postName');
            }
        });
    }
    // //角色查询
    queryRole() {
        //岗位标识及名称
        let param = {
            pageSize: "1000000"
        }
        this.commonHttpService.queryRoleListInfo(param).subscribe(data => {
            if (data.roleListInfo) {
                // 
                this.list4 = this.commfunc.listToLabelvalue(data.roleListInfo, 'roleDescribe', 'roleId');
                this.list5 = this.commfunc.listToLabelvalue(data.roleListInfo, 'roleId', 'roleDescribe');
            }
        });
    }
    //onchange事件
    doChose(event) {
        this.partyRoleBean.orgName = this.returnValue(event.value, 'label', 'value');
    }
    doChose1(event) {
        this.partyRoleBean.orgId = this.returnValue(event.value, 'value', 'label');
    }

    doChose2(event) {
        this.partyRoleBean.postId = this.returnValue1(event.value, 'label', 'value');

    }
    doChose3(event) {
        this.partyRoleBean.postName = this.returnValue1(event.value, 'value', 'label');
    }

    doChose4(event) {
        this.partyRoleBean.roleId = this.returnValue2(event.value, 'label', 'value');

    }
    doChose5(event) {
        this.partyRoleBean.roleDescribe = this.returnValue2(event.value, 'value', 'label');
    }
    /**
    * 转换值
    * @param param  值
    * @param colum  字段
    */
    returnValue(param, colum, valueColum) {
        let value: string = "";
        this.list.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }
    returnValue1(param, colum, valueColum) {
        let value: string = "";
        this.list2.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }
    returnValue2(param, colum, valueColum) {
        let value: string = "";
        this.list4.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }
    //查询
    queryClick() {
 
        this.commonHttpService.queryOrgPostRoleRelation(this.partyRoleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgPostRoleList;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    queryC() {
        this.partyRoleBean.flag = '0'
        this.commonHttpService.queryOrgPostRoleRelation(this.partyRoleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgPostRoleList;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //重置
    resetClick() {
        this.partyRoleBean = new PartyRoleBean()
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.partyRoleBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1 + '';
        this.partyRoleBean.pageNum = currentPage;
        //调用查询的方法
        this.queryClick();
        this.partyRoleBean.pageNum = '1';
    }

    // 增加
    add() {
        this.headerTitle = '添加角色';
        this.display = true;
        this.showModel = 1;
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        this.partyRoleBean.pageNum = '1';
        this.queryClick();
    }

    // 修改
    update(item) {
        this.headerTitle = '岗位角色修改';
        this.display = true;
        this.showModel = 2;
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        this.partyRoleBean = new PartyRoleBean();
        this.queryClick();
    }

    //删除
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                let param = { partyId: item.partyId, postId: item.postId, roleId: item.roleId };
                this.commonHttpService.deleteOrgPostRoleRelation(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.partyRoleBean.pageNum = '1';
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }

    //返回的值
    outValue(param) {
        this.display = param.display;
    }

}